import "./Tests.css";
import TestsCard from "./TestsCard";

const Tests = ({ activeTest }) => {
  return (
    <section className={activeTest ? "tests tests-click-style" : "tests"}>
      <h2 data-aos="fade-right" data-aos-duration="600" data-aos-once="true">
        Testlar
      </h2>
      <div className="row align-items-start">
        <TestsCard />
      </div>
    </section>
  );
};

export default Tests;
