import testsBase from "./TestsBase";

const TestsCard = () => {
  return (
    <>
      {testsBase.map((item) => (
        <div
          className="col-lg-4 col-md-6 col-12 mb-md-5 mb-4 tests-card"
          data-aos={item.aos}
          data-aos-duration="800"
          data-aos-delay={item.aosDelay}
          data-aos-once="true"
          key={item.id}
        >
          <div className="img-box">
            <img src={item.imgUrl} alt={item.title} />
          </div>
          <div className="text-box">
            <p className="date">Testlar soni {item.countTest} ta</p>
            <h3>{item.title}</h3>
            <p className="card-text">{item.info}</p>
            <a
              href={item.linkTest}
              target={item.linkTest === "no_test" ? "_self" : "_blank"}
              rel="noreferrer"
            >
              Testni boshlash
            </a>
          </div>
        </div>
      ))}
    </>
  );
};

export default TestsCard;
